export interface LetterModel {
  success: boolean;
  message: string;
  data: LetterData;
  errors: any[];
}

export interface LetterData {
  totalCount: number;
  page: number;
  pageSize: number;
  items: LetterItem[];
}

export interface LetterItem {
  id: number;
  direction: number; // 0: In, 1: Out
  letterType: number; // 0: Internal, 1: External
  subject: string;
  body: string;
  to: string;
  cc: string;
  bcc: string;
  letterDate: string;
  referenceNumbers: string;
  status?: number; // 0: Draft, 1: PendingApproval, 2: Approved, 3: Rejected, 4: Sent
  approvedByUserId?: string;
  approvedByName?: string;
  approvedAt?: string;
  approvalNotes?: string;
  signatureImagePath?: string;
  isEmailSent?: boolean;
  emailSentAt?: string;
  pdfPath?: string;
  createdByUserId: string;
  createdByName: string;
  createdAt: string;
  updatedAt: string;
  attachments: LetterAttachment[];
  // For internal letters
  internalUserId?: string;
  internalUserName?: string;
}

export interface LetterAttachment {
  id: number;
  letterId: number;
  filePath: string;
  fileType: string;
  fileName: string;
  uploadedByUserId: string;
  uploadedByName: string;
  description: string;
  uploadedAt: string;
  documentSource: number;
}

export interface CreateLetterDto {
  direction: number;
  letterType: number; // 0: Internal, 1: External
  subject: string;
  body: string;
  to: string;
  cc: string;
  bcc: string;
  letterDate: string;
  internalUserId?: string;
}

export interface UpdateLetterDto extends CreateLetterDto {
  id: number;
}

export interface LetterSearchParams {
  search?: string;
  PageSize?: number;
  PageNumber?: number;
  Confidentiality?: number;
  Direction?: number;
  Status?: number;
  From?: string;
  To?: string;
}

export interface AttachmentUploadDto {
  file: File;
  description?: string;
  documentSource?: number;
}

// Enums for better type safety
export enum LetterDirection {
  In = 0,
  Out = 1,
}

export enum LetterType {
  Internal = 0,
  External = 1,
}

export enum LetterConfidentiality {
  Public = 0,
  Restricted = 1,
  Secret = 2,
}

export enum LetterStatus {
  Draft = 0,
  PendingApproval = 1,
  Approved = 2,
  Rejected = 3,
  Sent = 4,
}

// New interfaces for approval workflow
export interface SubmitForApprovalResponse {
  success: boolean;
  message: string;
  data?: any;
  errors: any[];
}

export interface ApproveLetterRequest {
  signature?: File;
}

export interface RejectLetterRequest {
  rejectionReason?: string;
}

export interface SendEmailRequest {
  to?: string;
  cc?: string;
  bcc?: string;
  subject?: string;
  body?: string;
}
