import { Component, Input, OnInit } from '@angular/core';
import { LettersService } from '../../../services/letters.service';
import { TranslationService } from '../../../services/translation.service';
import { LetterItem, LetterAttachment } from '../../../models/letter.model';

@Component({
  selector: 'app-letter-details-modal',
  standalone: false,
  templateUrl: './letter-details-modal.component.html',
  styleUrl: './letter-details-modal.component.css',
})
export class LetterDetailsModalComponent implements OnInit {
  @Input() letterId: number | null = null;

  letter: LetterItem | null = null;
  isLoading = false;
  isVisible = false;

  constructor(
    private lettersService: LettersService,
    private translationService: TranslationService
  ) {}

  ngOnInit() {}

  open(letterId: number) {
    this.letterId = letterId;
    this.isVisible = true;
    this.loadLetterDetails();
  }

  close() {
    this.isVisible = false;
    this.letter = null;
    this.letterId = null;
  }

  loadLetterDetails() {
    if (!this.letterId) return;

    this.isLoading = true;
    this.lettersService.getLetter(this.letterId).subscribe({
      next: (response) => {
        if (response.success && response.data) {
          this.letter = response.data;
        }
        this.isLoading = false;
      },
      error: (error) => {
        console.error('Error loading letter details:', error);
        this.isLoading = false;
      },
    });
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  formatDate(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
  }

  getDirectionLabel(direction: number): string {
    const directions: Record<number, string> = {
      0: this.translate('letters.directions.0'),
      1: this.translate('letters.directions.1'),
    };
    return directions[direction] || 'Unknown';
  }

  getLetterTypeLabel(letterType: number): string {
    const letterTypes: Record<number, string> = {
      0: this.translate('letters.letter_types.0'),
      1: this.translate('letters.letter_types.1'),
    };
    return letterTypes[letterType] || 'Unknown';
  }

  getStatusLabel(status: number): string {
    const statuses: Record<number, string> = {
      0: this.translate('letters.statuses.0'),
      1: this.translate('letters.statuses.1'),
      2: this.translate('letters.statuses.2'),
      3: this.translate('letters.statuses.3'),
      4: this.translate('letters.statuses.4'),
    };
    return statuses[status] || 'Unknown';
  }

  getDirectionBadgeClass(direction: number): string {
    return direction === 0 ? 'bg-primary' : 'bg-success';
  }

  getLetterTypeBadgeClass(letterType: number): string {
    return letterType === 0 ? 'bg-info' : 'bg-warning';
  }

  getStatusBadgeClass(status: number): string {
    switch (status) {
      case 0:
        return 'bg-secondary'; // Draft
      case 1:
        return 'bg-warning'; // Pending
      case 2:
        return 'bg-success'; // Approved
      case 3:
        return 'bg-danger'; // Rejected
      case 4:
        return 'bg-info'; // Sent
      default:
        return 'bg-secondary';
    }
  }

  downloadAttachment(attachment: LetterAttachment) {
    // Implementation for downloading attachment
    console.log('Download attachment:', attachment);
  }

  viewAttachment(attachment: LetterAttachment) {
    // Implementation for viewing attachment
    console.log('View attachment:', attachment);
  }
}
